import { useUserInformationContext } from "../../containers/userInformationContext";import { ScanNotSupportedLocation } from "./scanNotSupportedLocation";import { ScanNotSupportedWebsite } from "./scanNotSupportedWebsite";const ScanNotSuccess = () => {  const { hostname } = useUserInformationContext();  if (hostname.search(".amazon.") !== -1) {    console.log("loooooooooooooool");    return <ScanNotSupportedLocation website={"Amazon"} />;  }  if (hostname.search(".ebay.") !== -1)    return <ScanNotSupportedLocation website={"Ebay"} />;  if (hostname.search(".aliexpress.") !== -1)    return <ScanNotSupportedLocation website={"AliExpress"} />;  return <ScanNotSupportedWebsite />;};export default ScanNotSuccess;